html {
  box-sizing: border-box; }

*,
::before,
::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
  font-weight: 400;
  font-family: "Lato", Arial, Helvetica, sans-serif;
  color: #3d3d3d;
  background-color: #f3f3f3; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
  /* added line */ }

[hidden] {
  display: none !important; }

.header {
  color: #fff;
  background-color: #222222; }

.header__top {
  display: flex;
  justify-content: space-between;
  min-height: 115px; }

.header__user-menu {
  margin: 15px 0 20px;
  justify-content: flex-end; }

.header__logo {
  margin-top: 18px; }

.header__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  max-width: calc(100% - 25.39% - 39px);
  padding-left: 100px; }

.logo__img {
  display: block; }

.nav {
  background-color: #f10e34; }

.nav__container {
  display: flex;
  justify-content: space-between;
  min-height: 51px; }

.nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  width: 100%;
  max-width: calc(100% - 25.39% - 39px);
  margin: 0;
  padding: 0; }

.nav__link {
  display: block;
  padding: 17px 0 16px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.3s ease; }
  .nav__link:hover {
    color: #ffc808; }

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.content__container {
  display: flex; }

.content__aside {
  flex-shrink: 0;
  width: 25.39%;
  margin-right: 39px; }

.aside__menu {
  margin: 0; }

.aside__block:not(:first-child) {
  margin-top: 30px; }

.aside-menu {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03);
  background-color: #fff;
  list-style: none;
  padding: 0 14px 9px; }

.aside-menu__link {
  display: block;
  padding: 15px 9px 16px 43px;
  color: #222222;
  text-decoration: none; }
  .aside-menu__link:hover {
    color: #f10e34; }

.aside-menu__item:not(:last-child) .aside-menu__link {
  border-bottom: 1px solid #eaeaea; }

.widget {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03);
  background-color: #fff;
  padding-left: 25px;
  padding-right: 25px; }

.widget__top {
  padding: 18px 0 16px;
  border-bottom: 1px solid #eaeaea; }

.widget__title {
  margin: 0; }

.widget__form {
  padding-top: 20px;
  padding-bottom: 30px; }

.widget__form-title {
  margin-top: 0;
  margin-bottom: 20px; }

.widget__form-control {
  margin-bottom: 23px; }

.title {
  font-size: 22px;
  line-height: 1.36;
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }

.user-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
  width: 100%;
  padding: 0;
  font-size: 12px;
  line-height: 1.5; }

.user-menu__item {
  display: flex;
  align-items: center; }
  .user-menu__item:not(:last-child):after {
    content: "|";
    margin: 0 18px; }

.user-menu__link {
  color: #fff;
  text-decoration: none; }
  .user-menu__link:hover {
    color: #f22f49; }

.promo {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03); }

.promo__img {
  display: block;
  transition: opacity 0.3s ease; }

.promo__link:hover .promo__img {
  opacity: 0.9; }

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 46px;
  padding: 13px 23px;
  border: 0;
  border-radius: 20px;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 700;
  font-family: inherit;
  text-transform: uppercase;
  color: #fff;
  background-color: #f22f49;
  transition: background-color 0.3s ease;
  outline: 0;
  cursor: pointer; }
  .button:disabled, .button--disabled {
    background-color: #919191;
    cursor: default;
    pointer-events: none; }
  .button:hover, .button:focus {
    background-color: #e00e2a; }

.input-text {
  width: 100%;
  display: block;
  height: 45px;
  padding: 13px 13px 12px;
  border: 1px solid transparent;
  border-radius: 5px;
  font: inherit;
  color: #000;
  background-color: #f3f3f3;
  outline: none; }
  .input-text:focus {
    border-color: #b6b6b6; }

.input-text_size_small {
  height: 37px;
  padding: 10px 10px 9px; }

.input-text_theme_dark {
  background-color: #fff; }

.input-error {
  display: none;
  color: #f10e34; }

.input-error_visible {
  display: block; }

.form-control__message {
  margin-top: 8px; }
